html {
  font-family: Verdana, Arial, Helvetica, sans-serif;
}

html, body {
  height: 100%;
}

label {
  cursor: pointer;
}

.wrapper {
  min-height: 100%;
  background-color: #f4f4f4;
  flex-direction: column;
  display: flex;
}

.container {
  max-width: 1200px;
  margin: 2.5% auto;
  overflow: hidden;
}

.header {
  height: 90px;
  z-index: 10;
  width: 100%;
  background: radial-gradient(circle, #eeaeca 0%, #94bbe9 100%);
  border-bottom: 1px solid gray;
  flex: 1 2 1px;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: sticky;
  top: 0;
  box-shadow: 2px 5px 10px gray;
}

.main-content {
  flex: auto;
}

.footer {
  min-height: 40px;
  max-height: 60px;
  width: 100%;
  background: linear-gradient(90deg, #eeaeca 0%, #94bbe9 100%);
  border-top: 2px solid gray;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  display: flex;
  position: sticky;
  bottom: 0;
  left: 0;
}

.cards-wrapper {
  grid-template-columns: repeat(auto-fill, 270px);
  justify-content: center;
  gap: 40px 30px;
  display: grid;
}

.card {
  width: 250px;
  min-height: 184px;
  max-height: 240px;
  background-color: #e8d8d8;
  border-radius: 5px;
  flex-direction: column;
  display: flex;
  position: relative;
  box-shadow: 5px 5px 10px #b3b3b3;
}

.card-img {
  object-fit: cover;
  height: 177px;
  background-image: url("https://www.freepik.com/free-photos-vectors/blue-merle-australian-shepherd");
  border-radius: 5px;
}

.card-btn {
  width: 25px;
  height: 25px;
  border: 1px solid gray;
  border-radius: 5px;
  font-weight: bold;
  position: absolute;
  top: 3px;
  right: 3px;
}

.card-btn:hover {
  background-color: #d66b6b;
}

.card-info {
  margin: 10px;
  display: flex;
}

.switcher__title {
  text-align: center;
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: bold;
}

.dropdown__text {
  color: #2f4f4f;
  text-align: center;
  font-weight: bold;
}

label input[name="display"] {
  display: none;
}

.switcher__group {
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  display: flex;
}

.switcher__item {
  width: 80px;
  height: 30px;
  border: 2px solid #000;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.active {
  background-color: #adff2f;
}

.mg__left {
  margin-left: 20px;
}

.filter {
  min-width: 200px;
  height: 70px;
  border: 2px solid #000;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  margin-left: 5%;
  margin-right: 5%;
  display: flex;
}

.filter__title {
  font-size: 16px;
  font-weight: bold;
}

.filter__item {
  margin-top: 2px;
  margin-bottom: 5px;
}

.refresh {
  min-width: 70px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 5%;
  margin-right: 5%;
  display: flex;
}

.refresh-btn {
  width: 60px;
  height: 60px;
  background-image: url("refresh.6fa21e1e.svg");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%;
  display: block;
  position: relative;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.refresh-btn:hover {
  animation: spin 2s linear infinite;
  transform: rotate(360deg);
}

.filter__panel {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.filter__title {
  margin-right: 20px;
}

.dropdown {
  margin-top: 2px;
  position: relative;
}

.dropdown:hover .dropdown__menu {
  display: block;
}

.dropdown__menu:after {
  content: "";
  width: 0;
  height: 0;
  border: 15px solid #0000;
  border-bottom-color: #b96cbe;
  display: block;
  position: absolute;
  top: -28px;
  left: 95px;
}

.dropdown__menu:before {
  content: "";
  height: 30px;
  display: block;
  position: absolute;
  top: -30px;
  left: 0;
  right: 0;
}

.dropdown__menu {
  white-space: wrap;
  text-align: center;
  opacity: .9;
  width: 160px;
  color: #111;
  background: #b96cbe;
  border-radius: 5px;
  margin-top: 15px;
  padding: 19px;
  font-weight: bold;
  line-height: 1.2;
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
}

.pagination {
  justify-content: center;
  display: flex;
}

.pagination__list {
  list-style-type: none;
  display: flex;
}

.pagination__item {
  width: 36px;
  height: 36px;
  cursor: pointer;
  background-color: #add8e6;
  border: 1px solid #000;
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  margin: 10px;
  transition: width .2s, height .2s;
  display: flex;
}

.pagination__item:hover {
  width: 42px;
  height: 42px;
  transition: width .2s, height .2s;
}

.pagination__item--active {
  background-color: #fc9d9d;
  font-weight: 800;
}

.preloader {
  z-index: 1001;
  background: #e0e0e0;
  position: fixed;
  inset: 0;
  overflow: hidden;
}

.preloader__image {
  width: 70px;
  height: 70px;
  text-align: center;
  margin-top: -35px;
  margin-left: -35px;
  animation: preloader-rotate 2s linear infinite;
  position: relative;
  top: 50%;
  left: 50%;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}

.loaded_hiding .preloader {
  opacity: 0;
  transition: opacity .3s;
}

.loaded .preloader {
  display: none;
}

.tree__title {
  margin-left: 50px;
}

.tree__list {
  margin-top: 10px;
  margin-left: 30px;
}

.card__item {
  width: 75px;
  height: 75px;
  cursor: pointer;
  background-repeat: no-repeat;
  background-size: cover;
  border: 1px solid gray;
  border-radius: 5px;
  margin: 3px;
  transition: border .2s;
  display: inline-block;
  box-shadow: 1px 1px 4px #b3b3b3;
}

.card__item.maximized {
  height: 100%;
  width: 100%;
  z-index: 100;
  background-size: cover;
  transition: all .3s ease-in-out;
  position: fixed;
  top: -5px;
  left: -5px;
}

.card__item.maximized:hover {
  content: "";
  border: none;
}

.card__item:hover {
  content: "";
  border: 3px solid #ff5757;
  transition: border .2s;
}

.tree__coll {
  max-width: 1000px;
  margin-top: 5px;
  margin-left: 50px;
}

.treeline span:hover {
  font-weight: bold;
}

.treeline span {
  cursor: pointer;
  font-size: 18px;
}

.treeline li {
  padding-top: 10px;
  position: relative;
}

.handleLi:before {
  content: "";
  height: 100%;
  border-left: 2px solid gray;
  position: absolute;
  top: -10px;
  left: -20px;
}

.handleLi:after {
  content: "";
  width: 19px;
  border-bottom: 2px solid gray;
  position: absolute;
  top: 19px;
  left: -19px;
}

.handleLi:last-child:before {
  height: 31px;
}

.hide:before {
  content: "";
  width: 20px;
  height: 20px;
  vertical-align: text-bottom;
  background-image: url("plus.966766c9.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%;
  margin-right: 5px;
  transition: background-image, all .2s;
  display: inline-block;
}

.show:before {
  content: "";
  width: 20px;
  height: 20px;
  vertical-align: text-bottom;
  background-image: url("minus.b894f78d.svg");
  background-repeat: no-repeat;
  margin-right: 5px;
  transition: background-image, all .2s;
  display: inline-block;
}

